<template>
  <div class="xzxc-emotion">
      <br>
      <el-form :label-position="this.$store.state.panelLayout" label-width="120px">
        <el-row :gutter="24">
          <el-col :lg="8" v-if="admin">
            <el-form-item label="选择用户：" label-width="100px">
              <get-user-data :getUserIdFn="getSetUser"></get-user-data> 
            </el-form-item>
          </el-col>
          <el-col :lg="8">
            <el-form-item label="搜索：" label-width="80px">
              


                  <el-input placeholder="选择搜索条件" v-model="pager.selectSearch" class="input-with-select">
                    <el-select v-model="pager.searchCondition" @change="selectSearchFn" slot="prepend" placeholder="请选择" style="width:100px;">
                      <el-option label="课时标题" value="title"></el-option>
                      <el-option label="订购数量" value="orderNumber"></el-option>
                      <el-option label="原价" value="originalPrice"></el-option>
                      <el-option label="现价" value="price"></el-option>
                      <el-option label="阅读量" value="readNumber"></el-option>
                      <el-option v-if="admin" label="管理员ID" value="user_id"></el-option>
                      <el-option v-if="admin" label="发布人ID" value="publisher_user_id"></el-option>
                      <el-option label="作者" value="author_name"></el-option>
                      <el-option label="发布时间" value="create_time"></el-option>
                      <el-option label="描述" value="emotion_describe"></el-option>
                      <el-option label="图文" value="text"></el-option>
                    </el-select>
                    <el-button slot="append" icon="el-icon-search" @click="query"></el-button>
                  </el-input>



            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <br> 
		  <el-table
		    :data="emotionData"
		    height="600"
		    border
		    style="width: 100%">

        <el-table-column
          label="状态" width="100" fixed>
           <template slot-scope="scope">
              <el-tag type="danger" v-if="scope.row.isDelete == 0">等待审核...</el-tag>
              <el-tag type="success" v-else>审核通过</el-tag>
              
           </template>
        </el-table-column>

        <el-table-column
          prop="author_name"
          label="作者">
        </el-table-column>

        <el-table-column
          prop="id"
          label="ID"
          width="180">
        </el-table-column>

        <el-table-column v-if="admin"
          prop="user_id"
          label="管理员ID"
          width="180">
        </el-table-column>

        <el-table-column v-if="admin"
          prop="publisher_user_id"
          label="发布人"
          width="180">
        </el-table-column>

        <el-table-column
          prop="min_picture"
          label="缩略图"
          width="180">
          <template slot-scope="scope">
            <img style="width:30px;height:30px;border-radius:15px;" :src="imgpath + '/' + scope.row.min_picture">
          </template>
        </el-table-column>


        <el-table-column
          prop="title"
          label="课时标题"
          width="180">
        </el-table-column>

		    <el-table-column
		      prop="orderNumber"
		      label="订购数量"
		      width="180">
		    </el-table-column>

        <el-table-column
          prop="originalPrice"
          label="原价"
          width="180">
        </el-table-column>

        <el-table-column
          prop="price"
          label="现价"
          width="180">
        </el-table-column>

        <el-table-column
          prop="price"
          label="现价"
          width="180">
        </el-table-column>

        <el-table-column
          prop="readNumber"
          label="阅读量"
          width="180">
        </el-table-column>

        

		    <el-table-column
		      prop="audio"
		      label="音频"
		      width="180">
		    </el-table-column>

        <el-table-column
          prop="video"
          label="视频"
          width="180">
        </el-table-column>

        <el-table-column
          prop="theme_id"
          label="主题ID"
          width="180">
        </el-table-column>

        <el-table-column
          prop="text"
          label="图文"
          width="180">
        </el-table-column>

		    

        <el-table-column
          prop="create_time"
          label="课时发布时间" width="180">
        </el-table-column>

        <el-table-column
          prop="emotion_describe"
          label="描述">
        </el-table-column>

        <el-table-column
          label="审核" width="180" fixed="right" v-if="admin">

          <template slot-scope="scope">

            <el-button size="mini" v-if="scope.row.isDelete == 0" type="success" icon="el-icon-check" @click="toExamine(scope.row, 'show')">审核通过</el-button>
            <el-button size="mini" v-else type="danger" icon="el-icon-delete" @click="toExamine(scope.row, 'delete')">禁用</el-button>
          </template>


        </el-table-column>


        <el-table-column
          label="编辑" width="180" fixed="right">

          <template slot-scope="scope">

            <el-button size="mini" v-if="scope.row.isDelete == 1" type="primary" icon="el-icon-edit" @click="edit(scope.row)">编辑</el-button>
            <el-button size="mini" v-else type="warning" icon="el-icon-loading" disabled>等待审核</el-button>
          </template>


        </el-table-column>

		  </el-table>
      <br>
      <div class="paging-box" style="text-align:right;">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="pager.currentPage3"
            :page-size="pager.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pager.count">
          </el-pagination>
      </div>
  </div>
</template>

<script>
import GetUserData from '../getUserData'
export default {
  components: {
    GetUserData
  },
  data () {
    return {
      emotionData: [],
      pager: {
        currentPage3: 1,
        count: 0,
        pageSize: 10,
        size: 0,
        user_name: '',
        selectSearch: '',
        searchCondition: 'title'
      },
      userid: 0,
      imgpath: this.$store.state.staticPath,
      admin: false
    }
  },
  created () {
  	this.getEmotionData()
    this.userInfo = this.$store.state.userInfo
    if (this.userInfo) {
      if (this.userInfo.level === 1) {
        this.admin = true
      }
    }
  },
  computed: {
   
  },
  methods: {
    edit (item) {
      this.$router.push({
        name: 'emotion',
        query: {
          emotion_id: item.id,
          user_id: item.user_id
        }
      })
    },
    toExamine (item,type) {
      var _this = this
        this.$axios({
             type: '$axios',
             obj: {
              url: _this.api.emotionToExamine,
              method: 'post',
              success (res) {
                if (res.data.code == 200) {
                  _this.alertsu(res.data.msg)
                  _this.getEmotionData()
                } else {
                  _this.alerter(res.data.msg)
                }
              },
              error () {
                _this.alertsu('用户禁用失败！')
              },
              data: {
                id: item.id,
                type: type
              },
              header: {
                'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
                'content-type': 'application/json; charset=utf-8'
              }
            }
        })
    },
    query () {
      this.getEmotionData()
    },
    selectSearchFn (val) {
      this.pager.searchCondition = val
    },
    getSetUser (user) {
      this.userid = user.id
      this.pager.size = 0
      this.pager.pageSize = 10
      this.pager.count = 0
      this.getEmotionData()
    },
    handleSizeChange (val) {
      this.pager.pageSize = val
      this.getEmotionData()
    },
    handleCurrentChange (val) {
      this.pager.size = val - 1
      this.getEmotionData()
    },
    getEmotionData () {
      var _this = this
        _this.$get({
          type: '$get',
          obj: {
            url: _this.api.getEmotionData,
            success: function (res) {
              if (res.data.code == 200) {
                _this.emotionData = res.data.data
                _this.pager.count = res.data.count
                _this.alertsu('获取菜单数据成功！','获取菜单数据',2000)
              }
              else {
                _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
              }

            },
            error: function () {
              _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
            }
          },
          param: {
            userid: _this.userid,
            adminlevel: this.adminlevel,
            size: _this.pager.size,
            sizes: _this.pager.pageSize,
            search: _this.pager.selectSearch,
            searchCondition: _this.pager.searchCondition
          }
      })
    }
  },
  updated () {
  },
  destroyed () {
  },
  deactivated () {
  },
  activated () {
  }
}
</script>
<style>
.xzxc-emotion .el-input__icon{
  line-height: inherit;
}
</style>



